body {
  font-family: Arial, sans-serif;
}

.search-form {
  text-align: center;
  margin: 20px;
}

.gallery {
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  padding: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: auto;
}

.info {
  text-align: center;
  padding: 10px;
}

.load-more {
  cursor: pointer;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 18px;
  display: block;
}

